export default {
  props: ["action", "status", "tableData", "taskList", "allCnt", "doneCnt"],
  data: function data() {
    return {
      newList: {},
      ListDetail: false,
      timer: null,
      checked: false,
      AllChecked: false,
      ids: []
    };
  },
  filters: {
    listStatus: function listStatus(val) {
      switch (val) {
        case "1":
          return "Waiting";

        case "2":
          return "Processing";

        case "3":
          return "Succeeded";

        default:
          return "Failed";
      }
    }
  },
  mounted: function mounted() {},
  methods: {
    openDetail: function openDetail(id) {
      var _this = this;

      this.newList = [];
      this.tableData.forEach(function (e) {
        if (id == e.id) {
          _this.newList = e;
          _this.ListDetail = true;
        }
      });
    },
    handleSelectionChange: function handleSelectionChange(val) {
      var _this2 = this;

      this.timer = null;
      this.ids = [];
      val.forEach(function (r) {
        _this2.ids.push(r.id);
      });
    },
    delItems: function delItems(item) {
      var _this3 = this;

      if (item) {
        this.ids.push(item.id);
      }

      if (this.ids.length == 0) {
        this.$elementMessage("Select the task", "error");
        return false;
      }

      this.$confirm("Do you want to delete tish task?", "Tips", {
        confirmButtonText: "Confirm",
        cancelButtonText: "Cancel",
        type: "warning"
      }).then(function () {
        _this3.$apiCall("api.Product.delImportTaskDetail", {
          ids: _this3.ids
        }, function (r) {
          if (r.ErrorCode == 9999) {
            _this3.ids = [];
            _this3.allChecked = false;

            _this3.$emit("getItem");

            _this3.$elementMessage("Delete Completed", "success");
          } else {
            _this3.$elementMessage(r.Message, "error");
          }
        });
      }).catch(function () {
        _this3.ids = [];
        return false;
      });
    }
  },
  components: {}
};